<template>
  <div class="blockdata">
    <div class="container pt-5 pb-5">
      <div class="row pt-5">
        <div class="col container">
          <div id="imgdiv">
            <img class=".img-fluid" src="../assets/Analyze-bro.svg" alt="..." />
          </div>
        </div>
        <div id="Intropara" class="col">
          <h2>Find all the informations regarding BTC Blockchain</h2>
          <p>Latest data at your service</p>
        </div>
        <div class="container">
          <div class="infoitem">
            <h3>BTC sent in the past 24 hours</h3>
            <span>{{ btcsent }} Satoshi</span>
          </div>
          <div class="infoitem">
            <h3>Number of transactions in the past 24 hours</h3>
            <span>{{ tc }}</span>
          </div>
          <div class="infoitem">
            <h3>Total Bitcoins in circulation</h3>
            <span>{{ totalbtc }}</span>
          </div>
          <div class="infoitem">
            <h3>Estimated time until the next block</h3>
            <span>{{ eta }} Seconds</span>
          </div>
          <div class="infoitem">
            <h3>Current block reward in BTC</h3>
            <span>{{ rwrd }} BTC</span>
          </div>
          <h1>Latest block data</h1>
          <div class="infoitem">
            <h3>Hash</h3>
            <span>{{ latesthsh }}</span>
          </div>
          <div class="infoitem">
            <h3>Index</h3>
            <span>{{ latestind }}</span>
          </div>
          <div class="infoitem">
            <h3>Height</h3>
            <span>{{ latestheight }}</span>
          </div>
          <div class="infoitem">
            <h3>Time</h3>
            <span>{{ latesttime }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Footer />
</template>
<script>
import Footer from "@/components/Footer.vue";
export default {
  name: "Blockdata",
  components: {
    Footer,
  },
  data() {
    return {
      totalbtc: "", //done
      latestind: "", //done
      latesthsh: "", //done
      latestheight: "", //done
      latesttime: "", //done
      tc: "", //done
      btcsent: "", //done
      eta: "", //done
      rwrd: "",
    };
  },
  methods: {
    async getLatestdata() {
      const res = await fetch("https://blockchain.info/latestblock");
      const data = await res.json();
      // console.log(data)
      this.latestind = data.block_index;
      this.latesthsh = data.hash;
      this.latestheight = data.height;
      this.latesttime = data.time;
    },
    async gettotalBTC() {
      const res = await fetch("https://blockchain.info/q/totalbc");
      const data = await res.json();
      // console.log(data)
      this.totalbtc = data;
    },
    async getTransacCount() {
      const res = await fetch("https://blockchain.info/q/24hrtransactioncount");
      const data = await res.json();
      // console.log(data)
      this.tc = data;
    },
    async getBTCsent() {
      const res = await fetch("https://blockchain.info/q/24hrbtcsent");
      const data = await res.json();
      this.btcsent = data;
    },
    async getETA() {
      const res = await fetch("https://blockchain.info/q/eta");
      const data = await res.json();
      this.eta = data;
    },
    async getReward() {
      const res = await fetch("https://blockchain.info/q/bcperblock");
      const data = await res.json();
      this.rwrd = data;
    },
  },
  mounted() {
    this.getLatestdata();
    this.gettotalBTC();
    this.getTransacCount();
    this.getBTCsent();
    this.getETA();
    this.getReward();
  },
};
</script>
